export default {
	data () {
		return {
		}
	},
	computed: {
		project () {
			return this.$store.state.productInfo.project
		},
		debtor () {
			return this.$store.state.productInfo.debtor
		},
		report () {
			return this.$store.state.productInfo.report
		},
	},
	created () {
		const id = this.$route.params.id
		this.$store.dispatch('getProductInfoById', { id })
	}
}



